//vuex状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

//全局注册
Vue.use(Vuex);

//创建Store对象
const store = new Vuex.Store({
    //状态数据
    state: {
        topics: [
            { name: 'all',   desc: '全部', },//selected:true},
            { name: 'good',  desc: '精华', },//selected:false},
            { name: 'share', desc: '分享', },//selected:false},
            { name: 'ask',   desc: '问答', },//selected:false},
            { name: 'job',   desc: '招聘', },//selected:false},
            { name: 'dev',   desc: '客户端测试', },//selected:false},
        ],
        //用户未登录时的显示登录
        unLoginedMoreMenus: { login: '登录' },
        //用户已登录时的显示登出
        loginedMoreMenus: {create:'发表新话题', center:'个人中心', msg: '信息通知', logout: '登出'},
        //用户登录的状态
        isLogined: false,
        // 登录用户名
        loginname:'',
        // accesstoken
        accesstoken:'',
        //登录id
        author_id:'',
        //显示显示更多变量
        isShowMoreMenus: false
    },
    //状态数据的计算属性
    getters: {
        //方法一：this.$store.state.属性名
        moreMenus: function (state) {
            if (state.isLogined) {
                //用户处理登录状态，返回登录后的更多菜单
                return state.loginedMoreMenus;
            } return state.unLoginedMoreMenus;
        },
        getTopicDescByTab:function(state){
            return function(tab){
                for(var i=0; i<state.topics.length;i++){
                    if(state.topics[i].name==tab){
                        return state.topics[i].desc;
                    }
                }
            }
        }
    },
    //修改同步数据的函数
    mutations,
    //异步数据
    actions
});

//对外输出模块
export default store;


