//引入
import Vue from 'vue'
import VueRouter from 'vue-router'
import TopicList from '../components/TopicList.vue'
import TestVux from '../components/TestVux.vue'
// @表示src路径
import Login from '@/components/Login.vue';
import Center from '@/components/Center.vue';
import Msg from '@/components/Msg.vue';
import Logout from '@/components/Logout.vue';
import Topic from '@/components/Topic.vue';
import User from '@/components/User.vue';
import Create from '@/components/Create.vue';
import Collections from '@/components/Collections.vue';
import Edit from '@/components/Edit.vue';

// 全局注册
Vue.use(VueRouter);


// 定义路由信息
const routes=[
  {path: '/', redirect:'/topiclist/all'},
  // {path: '/Testvux', component:TestVux,name:'testvux'},
  {path: '/topiclist/:name', component:TopicList,props:true},
  // 详情页面
  {path:'/topic/:id',component:Topic,props:true},
  // 登录页面
  {path:'/login',component:Login},
  // 个人中心
  {path:'/center',component:Center},
  //用户中心
  {path:'/user/:loginname',component:User,props:true},
  //收藏话题页面[也可以用嵌套路由]
  {path:'/user/:loginname/collections',component:Collections,props:true},
  // 消息通知
  {path:'/msg',component:Msg},
  // 退出页面
  {path:'/logout',component:Logout},
  // 发表新话题
  {path:'/create',component:Create},
  //编辑话题
  {path:'/topic/:id/edit',component:Edit,props:true},
]

// 创建路由管理器
const router=new VueRouter({
  routes
})

// 对外输出模板
export default router;


