export default {
    name:'center',
    //获取登录名
    computed:{
        loginname:function(){
            return this.$store.state.loginname;
        }
    },
created:function(){
    //跳转到用户页面 使用编程式路由跳转
    //this.$router.push这个方法不会向history添加一个新的记录，点击后退返回上一个界面
    //this.$router.replace,这个方法不会向history添加一个记录，点击后退返回上一个页面
    //上一个页面的记录不存在（代替）
    this.$router.replace('/user/'+this.loginname);
}
}



